import Token, { ValToken } from "./Token"

/**
 * An abstract class shared between lexers
 */
export default abstract class Lexer {
  text: string
  pos: number
  col: number
  line: number
  prev_col: number
  prev_line: number

  constructor(text: string) {
    this.text = text
    this.pos = 0

    this.col = 0
    this.line = 0
    this.prev_col = 0
    this.prev_line = 0
  }

  increment(amount = 1) {
    this.pos += amount
    this.col += amount
  }

  error(message: string) {
    let line = this.text.split('\n')[this.prev_line]
    let spacing = ''

    for (let i = 0; i < this.prev_col; i++) {
      spacing += ' '
    }

    return Error(
      `Lexer error\n${line}\n${spacing}^\nError at line: ${this.prev_line +
        1} col: ${this.prev_col + 1}\n${message}`
    )
  }

  current_char() {
    return this.text.charAt(this.pos)
  }

  eat(char: string) {
    if (this.current_char() === char) {
      this.pos += 1
    } else {
      throw this.error(`Expected ${char} found ${this.current_char()}`)
    }
  }

  number(): ValToken {
    let num = ''
    let separator = false

    while (this.current_char().match(/[0-9.]/)) {
      if (this.current_char() === '.') {
        if (separator) {
          break
        } else {
          separator = true
        }
      }

      num += this.current_char()
      this.increment()
    }

    let result = Number(num)
    if (isNaN(result)) {
      throw this.error(`Could not parse number: '${num}'`)
    }

    return {
      type: 'number',
      value: result,
    }
  }

  abstract next_token() : Token
}
